import { documentToPlainTextString } from '@contentful/rich-text-plain-text-renderer';
import PostListItem from './PostListItem';
import { trimDescription, getMediaThumbnail } from '../../src/utils';

function PostList({ items }) {
  return (
    <div className="posts">
      {items.length > 0
        ? items
            .sort(
              (a, b) => new Date(b.sys.createdAt) - new Date(a.sys.createdAt)
            )
            .map(p => {
              const thumbnail = getMediaThumbnail(p.fields.media);
              console.log('p.fields.media', p.fields.media);

              return (
                <PostListItem
                  key={p.fields.slug}
                  image={thumbnail}
                  title={p.fields.title}
                  url={`/posts/${p.fields.slug}`}
                  description={trimDescription(
                    documentToPlainTextString(p.fields.body)
                  )}
                  createdAt={p.sys.createdAt}
                />
              );
            })
        : null}

      <style jsx>{`
        .posts {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;
        }
      `}</style>
    </div>
  );
}

export default PostList;
